}
.page-body__wrapper {
  width: 320px;
  display: flex;
  flex-direction: column;
  font-family: "PT Sans";
  color: #000000;
  font-size: 18px;
  line-height: 21px;
  background-color: #ffffff;
  box-sizing: border-box;
  box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
  margin: 0 auto;
}
