}
.main-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 320px;
  position: relative;
}
.main-header__logo {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  z-index: 2;
}
@media (min-width: 768px) {
